  .jokes-form .custom-checkbox + label {
    position: relative;
    font-size: 18px;
    line-height: 26px;
    cursor: pointer;
    display: inline-flex;
    align-items: center;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none; }
  .jokes-form .custom-checkbox + label::before {
    content: '';
