.loader {
  margin: 20px auto;
  width: 50px;
  height: 50px;
  border: 8px solid #F8F8F8;
  border-radius: 50%;
  border-top: 8px solid #7392FF;
  -webkit-animation: spin 2s linear infinite;
          animation: spin 2s linear infinite; }

@-webkit-keyframes spin {
  0% {
    transform: rotate(0deg); }
  100% {
    transform: rotate(360deg); } }

@keyframes spin {
  0% {
    transform: rotate(0deg); }
  100% {
    transform: rotate(360deg); } }

